@font-face {
  font-family: "TT-Lakes--regular";
  src: url(./fonts/TTLakesCompressed-Regular.eot);
  src: local("TT Lakes Compressed Regular"), local("TTLakesCompressed-Regular"), url(./fonts/TTLakesCompressed-Regular.eot?#iefix) format("embedded-opentype"), url(./fonts/TTLakesCompressed-Regular.woff) format("woff"), url(./fonts/TTLakesCompressed-Regular.ttf) format("truetype");
  font-weight: normal;
  font-style: normal; }

html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

* {
  margin: 0;
  padding: 0; }

body {
  height: 100vh;
  background-color: #f8f8f8;
  font-family: "TT Lakes Compressed", sans-serif;
  color: #1b1c21; }

.srv {
  border-bottom: 32px solid transparent; }

.srv-header {
  text-transform: uppercase;
  font-family: "TT-Lakes--regular", sans-serif;
  padding: 16px 32px;
  background: linear-gradient(50deg, #f8f8f8 0%, #ffffff 98.61%); }

.srv__wrapper {
  overflow: hidden;
  position: relative; }

.srv__list {
  list-style: none;
  display: flex;
  margin-bottom: 24px;
  transform: translate(0); }

@media screen and (min-width: 330px) {
  .srv__list {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(224px, 1fr));
    grid-auto-flow: row;
    grid-gap: 16px 32px;
    height: 180px;
    overflow: hidden; } }

.srv--elem-pad {
  padding: 16px; }

@media screen and (min-width: 768px) {
  .srv--elem-pad {
    padding: 16px 24px; } }

@media screen and (min-width: 1120px) {
  .srv--elem-pad {
    padding: 16px 32px; } }

.srv__list--show-all {
  height: auto; }

.srv__item {
  /* width: 224px; */
  margin-right: 16px;
  display: flex;
  align-items: center;
  background-color: #ffffff;
  border: 1px solid #eaeaea;
  border-radius: 10px 10px; }

.srv__item--height-brand {
  height: 72px; }

.srv__item--height-technique {
  height: 160px; }

.srv__item:last-child {
  margin-right: 0; }

@media screen and (min-width: 330px) {
  .srv__item {
    /* width: 224px; */
    display: flex;
    align-items: center;
    background-color: #ffffff;
    border: 1px solid #eaeaea;
    border-radius: 10px 10px; } }

.srv__title {
  color: #1b1c21;
  font-family: "TT-Lakes--regular", sans-serif;
  font-size: 16px; }

.srv__title--pad {
  padding: 16px; }

.srv__link {
  display: flex;
  justify-content: space-between;
  flex-grow: 1;
  /* width: 224px; */
  width: 240px; }

.srv__link--height-technique {
  height: 100%; }

.srv__logo {
  margin-left: 16px;
  height: -moz-fit-content;
  height: fit-content;
  align-self: center; }

.srv__go {
  /* margin-right: 16px;
  width: 40px; */
  align-self: end;
  padding: 16px; }

.srv__show {
  margin: 16px;
  cursor: pointer; }

@media screen and (min-width: 768px) {
  .srv__show {
    margin: 24px; } }

@media screen and (min-width: 1120px) {
  .srv__show {
    margin: 32px; } }

.srv__show--arrow {
  display: inline-block;
  position: static; }

.srv__show--arrow--rotate {
  transform: translateY(5px) rotate(180deg); }

.srv__show--text {
  font-family: "TT-Lakes--regular", sans-serif;
  color: #1b1c21;
  font-size: 16px;
  font-weight: 500;
  line-height: 24px; }

.swiper-pannel {
  width: 204px;
  /* height: 12px; */
  display: flex;
  justify-content: center;
  /* float: left; */
  margin: 0 auto; }

